$cinzaClaro: #1d1d1d;
$cinzaEscuro: #171717;
$azulClaro: #69c6f4;
$azulEscuro: #007dff;

#linguagensEFerramentas {
    text-align: center;

    h2 {
        display: inline-block;
        font-size: 40px;
        font-weight: normal;
        margin-bottom: 50px;
        color: $azulClaro;
        padding: 6px 20px;
        border-left: 4px solid $azulEscuro;
        border-right: 4px solid $azulEscuro;
    }

    .swiper {
        margin-top: 40px;
        width: 100%;
        height: 240px;
        cursor: grab;

        :active {
            cursor: grabbing;
        }

        .swiper-slide {
            text-align: center;
            font-size: 18px;
            display: flex;
            flex-direction: column;
            align-items: center;

            span {
                font-size: 1.1em;
                color: white;
            }

            i {
                margin: 15px 10px 0;
                font-size: 70px;
                padding: 32px;
                border-radius: 50%;
                background-color: rgba(0, 0, 0, 0.233);
            }

            svg {
                @extend i;
                width: 70px;
                height: 70px;
                object-fit: contain;
                color: #fff;
            }
        }
    }
}

@media (max-width: 825px) {
    #linguagensEFerramentas {
        .swiper {
                height: 200px;

                .swiper-slide {
                    span {
                        font-size: 0.8em;
                    }

                    i {
                        margin: 15px 10px;
                        font-size: 50px;
                        padding: 27px;
                    }

                    svg {
                        width: 50px;
                        height: 50px;
                    }
                }
            }
    }
    
}

@media (max-width: 600px) {
    #linguagensEFerramentas {
        .swiper {
            height: 170px;
    
            .swiper-slide {
                span {
                    font-size: 0.7em;
                }
    
                i {
                    margin: 15px 10px;
                    font-size: 40px;
                    padding: 22px;
                }
    
                svg {
                    width: 40px;
                    height: 40px;
                }
            }
        }
    }
}

@media (max-width: 399px) {
    #linguagensEFerramentas {
        .swiper {
            height: 200px;
    
            .swiper-slide {
                span {
                    font-size: 0.8em;
                }
    
                i {
                    margin: 15px 10px;
                    font-size: 45px;
                    padding: 28px;
                }
    
                img {
                    width: 45px;
                    height: 45px;
                }
            }
        }
    }
}